:root {
  --b-green: #8dd0b7;
  --b-light-green: #c4d9d1;
  --b-black: #454545;
  --b-gray: #bcbcbc;
  --b-light-gray: #f5f5f5;
  --b-white: #ffffff;
  --s-green: #7bc7aa;
  --dark-gray: #4e4e4e;
  --s-light-green: #a2cdbd;
  --s-gray: #c4c4c4;
  --gray-green: #b4bfbb;
  --b-yellow: #d6af7d;
}

@font-face {
  font-family: "Inter";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-v11-latin_cyrillic-regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-v11-latin_cyrillic-500.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

*:focus-visible {
  outline: 2px solid var(--s-green);
  outline-offset: 2px;
}

html {
  font-size: 16px;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.page__body {
  margin: 0;
  font-family: "Inter", sans-serif;
  color: var(--b-black);
  background-color: var(--b-light-gray);
}

.page__main {
  padding-top: 90px;
  padding-bottom: 100px;
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  background-color: var(--b-light-gray);
  z-index: 10;
}

.header__wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  .header__wrapper {
    padding-left: 3.75%;
    padding-right: 3.75%;
  }
}

.header__logo {
  order: 1;
  max-width: 150px;
}
.header__logo img {
  display: block;
  max-width: 100%;
  height: auto;
}
@media only screen and (min-width: 768px) {
  .header__logo {
    max-width: unset;
  }
}

.header__user-block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  order: 3;
  margin-top: 10px;
  border-top: 2px solid var(--b-black);
}
@media only screen and (min-width: 1024px) {
  .header__user-block {
    order: 2;
    border: none;
    width: auto;
    margin-top: 0;
    margin-left: auto;
    justify-content: flex-end;
  }
}

.header__contrast {
  display: none;
  margin-right: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: var(--b-black);
  text-decoration: none;
  transition: color 0.5s ease;
}
.header__contrast:hover {
  color: var(--s-green);
}
@media only screen and (min-width: 768px) {
  .header__contrast {
    display: block;
  }
}

.header__search {
  position: relative;
  margin-right: 20px;
}
@media only screen and (min-width: 1440px) {
  .header__search {
    margin-right: 178px;
  }
}

.header__search-btn {
  display: block;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: var(--b-black);
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: color 0.5s ease;
}
.header__search-btn:hover {
  color: var(--s-green);
}

.header__search-form {
  position: absolute;
  display: none;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  overflow: hidden;
}
@media only screen and (min-width: 768px) {
  .header__search-form {
    right: 0;
    left: auto;
  }
}
.header__search-form.is-active {
  display: block;
}

.header__search-form-wrapper {
  position: relative;
  width: 296px;
}

.header__search-input {
  display: block;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 15px;
  padding-left: 54px;
  border: 1px solid transparent;
  background: #eaeaea;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: 0.01em;
  color: var(--b-black);
  border-radius: 4px;
  -webkit-appearance: none;
}
.header__search-input:focus {
  border-color: var(--s-green);
  -webkit-appearance: none;
}
.header__search-input::placeholder {
  color: #a2a2a2;
}
.header__search-input::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
.header__search-input::-ms-clear, .header__search-input::-ms-reveal, .header__search-input::-webkit-search-decoration, .header__search-input::-webkit-search-cancel-button, .header__search-input::-webkit-search-results-button, .header__search-input::-webkit-search-results-decoration {
  display: none;
}

.header__search-submit {
  padding: 0;
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  background-color: transparent;
  border: none;
}

.main-nav {
  position: relative;
  order: 2;
  flex-grow: 1;
}
@media only screen and (min-width: 1024px) {
  .main-nav {
    width: 100%;
    margin-top: 30px;
    padding-top: 30px;
    padding-bottom: 20px;
    border-top: 4px solid var(--b-black);
  }
}
@media only screen and (min-width: 1024px) {
  .main-nav {
    order: auto;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
.main-nav__list--open {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  padding-top: 100px;
  background-color: var(--b-light-gray);
  z-index: 9;
}
@media only screen and (min-width: 1024px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

@media only screen and (min-width: 1024px) {
  .main-nav__item {
    margin-right: 2.38%;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav__item--support {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
  }
}
@media only screen and (min-width: 1440px) {
  .main-nav__item {
    margin-right: 3.38%;
  }
}

.main-nav__link {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--b-black);
  text-decoration: none;
  display: flex;
  justify-content: center;
  padding: 20px;
}
.main-nav__link--green {
  color: var(--s-green);
}
@media only screen and (min-width: 1024px) {
  .main-nav__link {
    font-size: 14px;
    padding: 0;
  }
}
@media only screen and (min-width: 1440px) {
  .main-nav__link {
    font-size: 20px;
    transition: color 0.5s ease;
  }
  .main-nav__link:hover {
    color: var(--s-green);
  }
  .main-nav__link--green:hover {
    color: var(--b-black);
  }
}

.main-nav__toggle {
  background-color: transparent;
  border: none;
  display: block;
  padding: 0;
  position: relative;
  z-index: 10;
  margin-left: auto;
}
.main-nav__toggle .main-nav__icon-cross {
  display: none;
}
.main-nav__toggle--open .main-nav__icon-menu {
  display: none;
}
.main-nav__toggle--open .main-nav__icon-cross {
  display: block;
}
@media only screen and (min-width: 1024px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__icon {
  stroke: var(--b-black);
}

