    transform: rotate(0) translateX(0) translateY(0);
    width: 100%;
    max-width: 100%;
  }
}

.start__wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding-right: 8%;
  padding-top: 167px;
  padding-bottom: 60px;
  padding-left: 420px;
}
@media (max-width: 1023px) {
  .start__wrapper {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 50px;
  }
}

.start__text {
  line-height: 140%;
  color: #000000;
  font-size: clamp(0.875rem, 2vw + 1rem, 2.5rem);
}
.start__text p {
  margin: 0;
  margin-bottom: 55px;
}
.start__text a {
  color: inherit;
}

.start__logo img {
