  flex-wrap: wrap;
  margin-top: 38px;
}
.start__info p {
  display: flex;
  margin: 0;
  margin-right: 4%;
  padding-right: 4%;
  font-size: clamp(12px, 1vw + 1rem, 18px);
  color: #98aea6;
  position: relative;
}
@media (max-width: 767px) {
  .start__info p {
    margin-bottom: 6px;
    font-size: 13px;
    line-height: 16px;
